'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';
import {
    Search,
    MapPin,
    TrendingUp,
    Activity,
    ArrowRight,
    Plus,
    BarChart2,
    List
} from 'lucide-react';
import { Card, Button, Badge, Skeleton } from '@/components/ui';

interface Scan {
    id: string;
    keyword: string;
    status: string;
    gridSize: number;
    radius: number;
    frequency: string;
    createdAt: string;
    centerLat: number;
    centerLng: number;
}

interface DashboardData {
    scansCount: number;
    recentScans: Scan[];
}

export default function DashboardPage() {
    const [data, setData] = useState<DashboardData | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetch('/api/dashboard')
            .then(res => res.json())
            .then(setData)
            .catch(console.error)
            .finally(() => setLoading(false));
    }, []);

    return (
        <div className="max-w-6xl mx-auto space-y-8">
            {/* Header */}
            <div className="flex justify-between items-end">
                <div>
                    <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
                    <p className="text-gray-500 mt-1">Overview of your local ranking performance.</p>
                </div>
                <Link href="/scans/new">
                    <Button size="lg" className="shadow-sm">
                        <Plus className="w-4 h-4" />
                        New Ranking Report
                    </Button>
                </Link>
            </div>

            {/* Stats Overview */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
                <Card className="p-6 card-hover">
                    <div className="flex items-start justify-between">
                        <div>
                            <p className="text-sm font-medium text-gray-500">Total Scans</p>
                            <h3 className="text-3xl font-bold text-gray-900 mt-2">{loading ? '-' : data?.scansCount}</h3>
                        </div>
                        <div className="p-2 bg-blue-50 text-blue-600 rounded-lg">
                            <Activity size={20} />
                        </div>
                    </div>
                    <div className="mt-4 flex items-center text-sm">
                        <span className="text-emerald-600 font-medium flex items-center">
                            <TrendingUp size={14} className="mr-1" /> +12%
                        </span>
                        <span className="text-gray-400 ml-2">vs last month</span>
                    </div>
                </Card>

                <Card className="p-6 card-hover">
                    <div className="flex items-start justify-between">
                        <div>
                            <p className="text-sm font-medium text-gray-500">Avg Visibility</p>
                            <h3 className="text-3xl font-bold text-gray-900 mt-2">42%</h3>
                        </div>
                        <div className="p-2 bg-indigo-50 text-indigo-600 rounded-lg">
                            <BarChart2 size={20} />
                        </div>
                    </div>
                    <div className="mt-4 flex items-center text-sm">
                        <span className="text-emerald-600 font-medium flex items-center">
                            <TrendingUp size={14} className="mr-1" /> +5%
                        </span>
                        <span className="text-gray-400 ml-2">vs last month</span>
                    </div>
                </Card>

                <Card className="p-6 md:col-span-2 bg-blue-600 text-white border-none shadow-lg shadow-blue-200">
                    <div className="h-full flex flex-col justify-between">
                        <div className="flex items-start justify-between">
                            <div>
                                <h3 className="text-xl font-bold">Pro Tips</h3>
                                <p className="text-blue-100 text-sm mt-1">Increase your visibility by optimizing GMB categories.</p>
                            </div>
                            <div className="bg-white/10 p-2 rounded-lg backdrop-blur-sm">
                                <List className="text-white" size={20} />
                            </div>
                        </div>
                        <Link href="#" className="text-sm font-medium text-blue-100 hover:text-white flex items-center gap-1 mt-4">
                            Read Guide <ArrowRight size={14} />
                        </Link>
                    </div>
                </Card>
            </div>

            {/* Recent Scans Table */}
            <Card noPadding className="overflow-hidden">
                <div className="px-6 py-4 border-b border-gray-100 flex items-center justify-between bg-white">
                    <h2 className="text-lg font-bold text-gray-900">Recent Reports</h2>
                    <Link href="/scans" className="text-sm text-blue-600 hover:text-blue-700 font-medium">View All</Link>
                </div>

                <div className="overflow-x-auto">
                    <table className="w-full text-left">
                        <thead className="bg-gray-50 border-b border-gray-100">
                            <tr>
                                <th className="px-6 py-3 text-xs font-semibold text-gray-500 uppercase tracking-wider">Report Name / Keywords</th>
                                <th className="px-6 py-3 text-xs font-semibold text-gray-500 uppercase tracking-wider">Location</th>
                                <th className="px-6 py-3 text-xs font-semibold text-gray-500 uppercase tracking-wider">Frequency</th>
                                <th className="px-6 py-3 text-xs font-semibold text-gray-500 uppercase tracking-wider">Status</th>
                                <th className="px-6 py-3 text-xs font-semibold text-gray-500 uppercase tracking-wider text-right">Action</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-100 bg-white">
                            {loading ? (
                                [1, 2, 3].map(i => (
                                    <tr key={i}>
                                        <td className="px-6 py-4"><Skeleton className="h-4 w-32" /></td>
                                        <td className="px-6 py-4"><Skeleton className="h-4 w-24" /></td>
                                        <td className="px-6 py-4"><Skeleton className="h-4 w-16" /></td>
                                        <td className="px-6 py-4"><Skeleton className="h-4 w-20" /></td>
                                        <td className="px-6 py-4"><Skeleton className="h-8 w-8 ml-auto" /></td>
                                    </tr>
                                ))
                            ) : data?.recentScans.length === 0 ? (
                                <tr>
                                    <td colSpan={5} className="px-6 py-12 text-center text-gray-500">
                                        No reports found. Start tracking now!
                                    </td>
                                </tr>
                            ) : (
                                data?.recentScans.map((scan) => (
                                    <tr key={scan.id} className="hover:bg-gray-50 transition-colors">
                                        <td className="px-6 py-4">
                                            <div className="flex items-center gap-3">
                                                <div className="w-8 h-8 rounded bg-gray-100 flex items-center justify-center text-gray-500">
                                                    <Search size={14} />
                                                </div>
                                                <div>
                                                    <div className="font-semibold text-gray-900">{scan.keyword}</div>
                                                    <div className="text-xs text-gray-500">{new Date(scan.createdAt).toLocaleDateString()}</div>
                                                </div>
                                            </div>
                                        </td>
                                        <td className="px-6 py-4">
                                            <div className="flex items-center text-sm text-gray-600">
                                                <MapPin size={14} className="mr-1 text-gray-400" />
                                                {scan.centerLat.toFixed(2)}, {scan.centerLng.toFixed(2)}
                                            </div>
                                        </td>
                                        <td className="px-6 py-4">
                                            <span className="text-xs font-semibold px-2 py-1 bg-gray-100 text-gray-600 rounded">
                                                {scan.frequency}
                                            </span>
                                        </td>
                                        <td className="px-6 py-4">
                                            <Badge variant={
                                                scan.status === 'COMPLETED' ? 'success' :
                                                    scan.status === 'RUNNING' ? 'blue' : 'default'
                                            }>
                                                {scan.status}
                                            </Badge>
                                        </td>
                                        <td className="px-6 py-4 text-right">
                                            <Link href={`/scans/${scan.id}`}>
                                                <Button variant="secondary" size="sm">
                                                    View Report
                                                </Button>
                                            </Link>
                                        </td>
                                    </tr>
                                ))
                            )}
                        </tbody>
                    </table>
                </div>
            </Card>
        </div>
    );
}
